function companiesSlider() {
    var swiper = new Swiper('.companies .swiper-container', {
        slidesPerView: 6,
        spaceBetween: 0,
        grid: {
            fill: 'row',
            rows: 2,
        },
        navigation: {
            nextEl: '.companies .swiper-button-next',
            prevEl: '.companies .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                grid: {
                    fill: 'row',
                    rows: 3,
                },
            },
            576: {
                slidesPerView: 4,
            },
            993: {
                slidesPerView: 6,
            },
        },
    })
}


function sliders() {
    let SlidersArray = []
    $(".slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
        })
    })
}

companiesSlider()
sliders()
