$("._call-me").click(function () {
    $(".call-modal").addClass("_active")
    $("body").addClass("fixed-body")

})


$(".call-modal__close").click(function () {
    $(".call-modal").removeClass("_active")
    if(!$(".header").hasClass("_open")){
        $("body").removeClass("fixed-body")
    }
})

$(".burger").click(function () {
    openMenu()
})

$(".menu__close").click(function () {
    closeMenu()
})

function openMenu(){
    $(".header").addClass("_open")
    $("body").addClass("fixed-body")
}

function closeMenu(){
    $(".header").removeClass("_open")
    $("body").removeClass("fixed-body")
}


let fancybox = document.querySelectorAll("[data-fancybox]")

Fancybox.defaults.placeFocusBack = false
