$(".fg input").each(function(index, el) {
    InputIsFilled($(el))
});

$(".fg textarea").each(function(index, el) {
    InputIsFilled($(el))
});


$(".fg select").each(function(index, el) {
    InputIsFilled($(el))
});

$(".fg input").change(function() {
    InputIsFilled($(this))
})

$(".fg textarea").change(function() {
    InputIsFilled($(this))
})

$(".fg select").change(function() {
    InputIsFilled($(this))
})

function InputIsFilled(el) {
    if ($(el).val()) {
        $(el).addClass("input-active")
    } else {
        $(el).removeClass("input-active")
    }
}

$(".lang__show").click(function () {
    $(this).parents(".lang").toggleClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('.lang');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(".lang").hasClass("_active")){
            $(".lang").removeClass("_active")
        }
    }
});

$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})

$(".tab").click(function() {
    let parentBlock = $(this).parents(".tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find(".tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})
