window.addEventListener('DOMContentLoaded', () => {
    gsap.registerPlugin(ScrollTrigger);
    if(document.querySelector(".index-page")){
        animationIndex()
    }
    if(document.querySelector(".blog-page")){
        animationBlog()
    }
    if(document.querySelector(".blog-item-page")){
        animationBlogItem()
    }
    if(document.querySelector(".about-page")){
        animationAbout()
    }
    if(document.querySelector(".faq-page")){
        animationFaq()
    }
    if(document.querySelector(".faq-page2")){
        animationFaq2()
    }
    if(document.querySelector(".test")){
        testAnimation()
    }

})

function animationIndex(){
    const tlHeader = gsap.timeline({})
    tlHeader.from('.main .display1', {
        y: 20,
    }).from('.main__text', {
        y: 20,
    },'<').from('.main__nav', {
        y: 20,
    },'<').from('.main__img', {
        y: 20,
        x: 30,
    },'<').from('.main .video-btn', {
        y: 20,
        x: 30,
    },'<')


    const advantageCards = Array.from(document.querySelectorAll('.advantage-card'));

    advantageCards.forEach(card => {
        cardAnimation(card)
    })

    const functionCards = Array.from(document.querySelectorAll('.function-card'));

    functionCards.forEach(card => {
        cardAnimation(card)
    })


    gsap.from('.functions .display3, .example', {
        y: 50,
        scrollTrigger: {
            trigger: '.functions',
            start: 'top bottom',
            toggleActions: 'play none none reverse'
        }
    })

    const tlSecure = gsap.timeline({
        scrollTrigger: {
            trigger: '.secure',
        }
    })
    tlSecure.from('.secure .display3', {
        y: 50,
    }).from('.secure .video-btn', {
        y: 50,
    },'<').from('.secure__img', {
        y: 50,
    },'<').from('.secure__list li', {
        y: 50,
        stagger: 0.2,
    }).from('.secure__hint', {
        y: 50,
    })

    const tlType = gsap.timeline({
        scrollTrigger: {
            trigger: '.type',
        }
    })
    tlType.from('.type .display3', {
        y: 50,
    })

    const typeCards = Array.from(document.querySelectorAll('.type-card'));

    typeCards.forEach(card => {
        cardAnimation(card)
    })


    const tlCompanies = gsap.timeline({
        scrollTrigger: {
            trigger: '.companies',
        }
    })
    tlCompanies.from('.companies .display3', {
        opacity: 0,
    }).from('.company-card', {
        opacity: 0,
        stagger: 0.1,
    })

    const tlTariffs = gsap.timeline({
        scrollTrigger: {
            trigger: '.tariffs',
        }
    })
    tlTariffs.from('.tariffs .display3', {
        y: 50,
    }).from('.tariffs__desc', {
        y: 50,
    })

    const tariffCards = Array.from(document.querySelectorAll('.tariff-card'));

    tariffCards.forEach(card => {
        cardAnimation(card)
    })


    const tlConnect = gsap.timeline({
        scrollTrigger: {
            trigger: '.connect',
        }
    })
    tlConnect.from('.connect .display3', {
        y: 50,
    }).from('.connect__img', {
        y: 100,
    },"<").from('.connect__desc', {
        y: 50,
    }).from('.connect__nav', {
        y: 50,
    })
}

function animationBlog(){
    const tlHeader = gsap.timeline({})
    tlHeader.from('.blog-main .display2', {
        y: 20,
    }).from('.blog-main .text18', {
        y: 20,
    },'<').from('.socials', {
        y: 20,
    },'<')

    const blogCards = Array.from(document.querySelectorAll('.blog-card'));

    blogCards.forEach(card => {
        cardAnimation(card)
    })
}

function animationBlogItem(){
    gsap.from('.blog-item .display2', {
        yPercent: 120,
    })
    const tlNews = gsap.timeline({
        scrollTrigger: {
            trigger: '.news-top',
            toggleActions: 'play none none reverse'
        }
    })
    tlNews.from('.news__media', {
        y: 100,
    })
}

function animationAbout(){
    const tlAboutMain = gsap.timeline({
        scrollTrigger: {
            trigger: '.about-main',
            toggleActions: 'play none none reverse'
        }
    })
    tlAboutMain.from('.about-main .display2', {
        y: 30,
    }).from('.about-main__text', {
        y: 30,
    },'<').from('.about-main__img', {
        y: 50,
        x: 60,
    },'<')

    const tlAboutCompany = gsap.timeline({
        scrollTrigger: {
            trigger: '.about-company',
            toggleActions: 'play none none reverse'
        }
    })
    tlAboutCompany.from('.about-company__title', {
        y: 30,
    }).from('.about-company__video', {
        y: 30,
    })

    const teamCards = Array.from(document.querySelectorAll('.team-card'));

    teamCards.forEach(card => {
        cardAnimation(card)
    })

    const opinionCards = Array.from(document.querySelectorAll('.opinion-card'));

    opinionCards.forEach(card => {
        cardAnimation(card)
    })

    const experienceCards = Array.from(document.querySelectorAll('.experience-card'));

    experienceCards.forEach(card => {
        cardAnimation(card)
    })
}

function animationFaq(){
    faqMainAnimation()

    const guideCards = Array.from(document.querySelectorAll('.guide-card'));

    guideCards.forEach(card => {
        cardAnimation(card)
    })

    const qaCards = Array.from(document.querySelectorAll('.qa-card'));

    qaCards.forEach(card => {
        cardAnimation(card)
    })
}

function animationFaq2(){
    faqMainAnimation()

    const screenCards = Array.from(document.querySelectorAll('.screen-img'));

    screenCards.forEach(card => {
        cardAnimation(card)
    })
}

function cardAnimation(card) {
    gsap.from(card, {
        y: 50,
        scrollTrigger: {
            trigger: card,
            start: 'top bottom',
            toggleActions: 'play none none reverse'
        }
    })
}

function testAnimation() {
    const tlTest = gsap.timeline({
        scrollTrigger: {
            trigger: '.test',
        }
    })
    tlTest.from('.test__img', {
        y: 50,
    }).from('.test__title', {
        y: 50,
    },'<').from('.test text18', {
        y: 50,
    },'<').from('.test__nav', {
        y: 50,
    })
}

function faqMainAnimation() {
    const tlAboutMain = gsap.timeline({
        scrollTrigger: {
            trigger: '.about-main',
            toggleActions: 'play none none reverse'
        }
    })
    tlAboutMain.from('.about-main .display2', {
        y: 30,
    }).from('.about-main__text', {
        y: 30,
    },'<').from('.about-main search-field', {
        y: 30,
    },'<')
}
