$(document).ready(function() {
    $('input[type="tel"]').mask('+7 999 999-9999', { autoclear: false }, { placeholder: '+7             ' });





});

let langs = document.querySelectorAll(".lang")

langs.forEach(lang => {
    lang.querySelector(".lang__show").onclick = function () {
        console.log(true)
        this.classList.toggle("_active")

        onClickClose(lang)

        function onClickClose(elem) {
            function outsideClickListener(event) {
                if (!elem.contains(event.target) && isVisible(elem)) {
                    lang.querySelector(".lang__show").classList.remove("_active")
                    document.removeEventListener('click', outsideClickListener);
                }
            }
            document.addEventListener('click', outsideClickListener)
        }
        function isVisible(elem) {
            return !!elem && !!( elem.offsetWidth || elem.offsetHeight || elem.getClientRects().length );
        }
    }

})
